import { mapState } from "vuex";
import AppDiyForm from "../../../components/page-component/app-diy-form/app-diy-form";

export default {
    data() {
        return {
            status: null,
            mallName: '',
            name: '',
            phone: '',
            parent_name: '',
            height: 0,
            form: {},
            template_message: [],
            read: false,
            protocol: false
        }
    },
    computed: {
        ...mapState({
            mall: state => state.mallConfig.mall,
            custom_setting: state => state.mallConfig.share_setting_custom,
            share_setting: state => state.mallConfig.share_setting,
        })
    },
    components: {
        AppDiyForm
    },
    methods: {
        handleFormInput({data}) {
            const result = [];
            for (let i in data) {
                result[i] = {
                    key: data[i].key,
                    label: data[i].name,
                    value: data[i].value,
                    required: data[i].is_required,
                };
            }
            this.form = result;
        },
        toRead() {
            this.read = !this.read
        },

        imageLoad: function (e) {
            var $width = e.detail.width; //获取图片真实宽度
            var $height = e.detail.height; //获取图片真实高度
            this.height = $height * (702 / $width)
        },
        subscribe() {
            let that = this;
            let read = 0;
            if(this.form.length > 0) {
                for(let item of this.form) {
                    if(item.required == 1) {
                        if(!item.value) {
                            uni.showToast({
                                title: '请填写' + item.label,
                                icon: 'none',
                                duration: 1000
                            });
                            return false
                        }
                        if(item.key == 'img_upload' && Array.isArray(item.value)) {
                            for(let img of item.value) {
                                if(!img) {
                                    uni.showToast({
                                        title: '请填写' + item.label,
                                        icon: 'none',
                                        duration: 1000
                                    });
                                    return false
                                }
                            }
                        }
                    }
                }
            }
            if (this.read == true) {
                read = 1;
                if (!that.name) {
                    uni.showToast({
                        title: '请输入真实姓名',
                        icon: 'none',
                        duration: 1000
                    });
                } else if (!that.phone) {
                    uni.showToast({
                        title: '请输入手机号码',
                        icon: 'none',
                        duration: 1000
                    });
                } else if (!(/0?(1)[0-9]{10}/.test(that.phone))) {
                    uni.showToast({
                        title: '请输入正确的手机号码',
                        icon: 'none',
                        duration: 1000
                    });
                } else {
                    // let template_message = [this.template_message]
                    this.$subscribe(this.template_message).then(res => {
                        this.submit(read);
                    }).catch(res => {
                        this.submit(read);
                    });
                }
            } else {
                uni.showToast({
                    title: '请先查看分销协议并同意',
                    icon: 'none',
                    duration: 1000
                });
            }
        },
        submit(read) {
            let that = this;
            uni.showLoading({
                title: '提交中...'
            });
            let para = {
                name: that.name,
                mobile: that.phone,
                agree: read
            }
            if(that.share_setting.form_status == 1) {
                para.form = JSON.stringify(that.form)
            }
            that.$request({
                url: that.$api.share.apply,
                data: para,
                method: 'post'
            }).then(response => {
                that.$store.dispatch('mallConfig/actionResetConfig');
                that.$hideLoading();
                if (response.code == 0) {
                    uni.showToast({
                        title: response.msg,
                        duration: 1000
                    });
                    setTimeout(function() {
                        uni.navigateBack({
                            delta: 1
                        })
                    }, 500)
                } else {
                    if(that.share_setting.form_status == 0 && response.msg == '请填写表单') {
                        uni.showToast({
                            title: '申请条件有所变化，请重新填写',
                            icon: 'none',
                            duration: 1000
                        });
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }
            }).catch(e => {
                that.$hideLoading();
                uni.showToast({
                    title: response,
                    icon: 'none',
                    duration: 1000
                });
            })
        }
    },

    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        this.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        if (options.template_message != null) {
            that.template_message = JSON.parse(options.template_message)
        }
        uni.setNavigationBarTitle({
            title: this.custom_setting.apply.share_apply.name ? this.custom_setting.apply.share_apply.name : this.custom_setting.apply.share_apply.default
        });
        this.$request({
            url: this.$api.user.user_info,
        }).then(response => {
            this.$hideLoading();
            if (response.code === 0) {
                this.parent_name = response.data.identity.parent_name
            }
        })
    },
    onShow() {
        this.$store.dispatch('mallConfig/actionResetConfig');
    }
}
